import React, { Component } from 'react';

export class Film extends Component {

    constructor(props) {
        super(props);
        this.film = props.film;
    }
    render() {
        return (<section>
          <h1>{this.film.title}</h1>
          <img src={`http://image.tmdb.org/t/p/w185/${this.film.poster_path}`} alt="movie poster" />
          <article>{this.film.overview}</article>
        </section>)
    }
}


